@import "../../styles/varibales";
.edit-profile {
  .cover {
    position: relative;
    width: 100%;
    height: 265px;
    background-color: $primarycolor;
    background-image: url("../../images/cover-bg.gif");
    border-bottom: 10px solid #000;
    box-shadow: 0px 10px 30px 5px;
    margin-bottom: 100px;
    @media (max-width: 767px) {
      height: 150px;
      margin-bottom: 50px;
    }
    .info {
      position: absolute;
      top: 33%;
      display: flex;
      align-items: center;
      gap: 30px;
      @media (max-width: 767px) {
        gap: 10px;
        top: 50%;
      }
      .profile-image {
        border: 10px solid #000;
        max-width: 300px;
        border-radius: 50%;
        box-shadow: 0px 15px 20px -3px;
        width: 35%;
        @media (max-width: 767px) {
          width: 100px;
          border-width: 5px;
        }
      }
      // .uploadcare--widget__button {
      //   opacity: 0;
      //   width: 40px;
      //   position: absolute;
      //   overflow: hidden;
      //   top: 0px;
      //   left: -3px;
      // }
      .crop-img{
        opacity: 0;
        width: 40px;
        position: absolute;
        overflow: hidden;
        top: 0px;
        left: -3px;
      }
      .edit-photo {
        position: absolute;
        left: 32%;
        top: 70%;
        transform: translate(-50%, -50%);
        width: 40px;
        height: 40px;
        background-color: #fff;
        border-radius: 50%;
        border: 2px solid $primarycolor;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        color: $primarycolor;
      }
      h2 {
        font-size: clamp(16px, 3vw, 40px);
        font-weight: bold;
        text-shadow: 0px 4px 13px;
        width: max-content;
      }
    }
    .share {
      position: absolute;
      top: 20%;
      right: 3%;
      @media (max-width:767px) {
        top: 3%;
      }
      .link {
        display: none;
      }
      button {
        border: none;
        outline: none;
        background-color: transparent;
        img {
          width: 200px;
          @media (max-width:767px) {
            width: 100px;
          }
        }
      }
    }
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    .row {
      input,
      textarea {
        color: #fff;
        background-color: #000;
        border-radius: 10px;
        padding-left: 20px;
        font-size: clamp(14px, 1vw, 16px);
      }
      textarea {
        height: 100px;
        resize: none;
      }
      label {
        color: #fff;
        margin-left: 20px;
        font-size: clamp(14px, 1vw, 16px);
      }
    }
    a {
      margin: 50px 0 -50px 0;
      text-decoration: underline;
      font-size: 20px;
      color: #000;
    }
  }
}
@import "submitbtn";
